import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";
import { useState } from "react";

// AIzaSyD1nehP8FaokWwthWusg7ifYgdP10uw_ic

const Iframe = ({ iframe, hideSpinner }) => {
    return (
        < div onLoad={hideSpinner}
            dangerouslySetInnerHTML={{ __html: iframe }}
        />
    )
}

const LocationCard = ({ item, iframe }) => {

    const [isLoading, setIsLoading] = useState(true)

    const hideSpinner = () => {
        setIsLoading(() => false)
    }

    return (
        <div className="flex">
            <div className="h-60 w-60">
                <div className="absolute bg-gray-300 opacity-80">
                    <h3>{item.title}</h3>
                    <p>{`${item.street}, ${item.postCode} ${item.city}`}</p>
                </div>
                <img className="object-cover h-full w-full" src="https://www.agra-risuty.cz/uploads/photos/ledce.jpg"></img>
            </div>
            {isLoading && <Loader
                type="Puff"
                color="#00BFFF"
                height={100}
                width={100}
            // timeout={5000} //5 secs
            />}
            <Iframe onLoad={hideSpinner} iframe={iframe} />

        </div>
    )
}

export default LocationCard
