import Slider from 'react-touch-drag-slider'
import { useState } from 'react'
import images from './images'

// {"access_token":"276911294492916|mVlOx5_gp5YUkgQB4XSgKkQ6bYs","token_type":"bearer"}

function Hero() {

    const [index, setIndex] = useState(0)

    const setFinishedIndex = (i) => {
        setIndex(i)
    }

    const next = () => {
        if (index < images.length - 1) setIndex(index + 1)
        if (index === images.length - 1) setIndex(0)
        console.log(index)
    }

    const previous = () => {
        if (index > 0) setIndex(index - 1)
        if (index === 0) setIndex(images.length - 1)
        console.log(index)

    }

    return (
        <div className='hero h-screen relative -mt-16'>

            <button className="absolute left-0 top-2/4 z-10 text-5xl inline-block hover:bg-gray-300 p-4 transition-all" onClick={previous}>
                
            </button>
            <button className="absolute right-0 top-2/4 z-10 text-5xl inline-block hover:bg-gray-300 p-4" onClick={next}>
                
            </button>
            <Slider
                onSlideComplete={setFinishedIndex}
                onSlideStart={(i) => {
                    // console.clear()
                    // console.log('started dragging on slide', i)
                }}
                activeIndex={index}
                threshHold={100}
                transition={0.5}
                scaleOnDrag={false}
            >
                {images.map(({ url, title }, index) => (
                    <img src={url} key={index} alt={title} />
                ))}
            </Slider>
        </div>


    )
}



export default Hero